/**
 * The MarkerLayer allows you to add markers to the map. You can specify your own markers
 * or add custom ones.
 *
 * @module Map
 * @class TomTomMarkersLayer
 * @extends L.FeatureGroup
 */
define('display/layers/markersLayer',['../map', '../svgIcon', '../objectUtils', './defaultIconsMappingProvider', 'leaflet.markercluster'], function(L, svgIcon, objectUtils, defaultIconsMappingProvider) {
    'use strict';

    var defaultOptions = {
        background: {
            iconSize: L.Browser.retina ? [50, 60] : [30, 36],
            iconAnchor: L.Browser.retina ? [25, 60] : [15, 36],
            icon: 'icon-marker-black',
            noPlainSVG: false
        },
        icon: {
            iconSize: L.Browser.retina ? [50, 50] : [30, 30],
            iconAnchor: [0, 0],
            icon: 'icon-flag-white',
            noPlainSVG: false
        },
        popupAnchor: L.Browser.retina ? [0, -50] : [0, -30],
        opacity: 0.8
    };

    var icons = defaultIconsMappingProvider.defaultIcons;
    var singleColorIcons = defaultIconsMappingProvider.singleColorIcons;

    function shouldUseSingleColorIcon(classification) {
        return objectUtils.hasOwnProperty(singleColorIcons, classification);
    }

    function getClassification(point) {
        var classification = 'default';
        if (hasClassification(point)) {
            classification = point.poi.classifications.length > 0 ? point.poi.classifications[0].code : 'default';
        } else if (isLowBandwidth(point)) {
            classification = point.cst.length > 0 ? point.cst[0].cd : 'default';
        }
        return classification;
    }

    function getMarkerIcon(classification, point, color) {
        var iconName = icons[classification] || icons.default;
        if (shouldUseSingleColorIcon(classification)) {
            defaultOptions.icon.icon = 'icon-' + iconName;
        } else {
            defaultOptions.icon.icon = 'icon-' + iconName + '-' + color;
            if (color === 'black') {
                defaultOptions.background.icon = 'icon-marker-white';
            }
        }
        return defaultOptions;
    }

    function getDefaultIcon(point, color) {
        var classification, iconOptions;
        classification = getClassification(point);
        iconOptions = getMarkerIcon(classification, point, color);
        return new svgIcon(iconOptions);
    }

    function defaultPopupContent(point) {
        var result = '';
        if (point.poi && point.poi.name) {
            result = point.poi.name + ' ';
        }
        if (point.address && point.address.freeformAddress) {
            result += point.address.freeformAddress
        }
        return result;
    }

    function isLowBandwidth(point) {
        return point.a2 !== undefined;
    }

    function hasClassification(point) {
        return objectUtils.hasOwnProperty(point, 'poi') && objectUtils.hasOwnProperty(point.poi, 'classifications');
    }

    function createMarker(point, options) {
        var markerPosition, popupHoverContent, popupContent, color, icon;
        markerPosition = point.position ?
                    [point.position.lat, point.position.lon] :
                    point.ps.split(' ');
        popupHoverContent = objectUtils.hasOwnProperty(options, 'popupHoverContent') ? options.popupHoverContent(point) : undefined;
        popupContent = objectUtils.hasOwnProperty(options, 'popupContent') ? options.popupContent(point) : defaultPopupContent(point);
        color = options.useBlackIcons ? 'black' : 'white';
        icon = objectUtils.hasOwnProperty(options, 'icon') ? options.icon(point) : getDefaultIcon(point, color);
        return {
            markerPosition: markerPosition,
            popupHoverContent: popupHoverContent,
            popupContent: popupContent,
            color: color,
            icon: icon
        }
    }

    L.TomTomMarkersLayer = L.FeatureGroup.extend({
        /**
         * Sets markers data, which will be used by addMarkers method. This method expects array of points returned by
         * one of our search calls e.g. fuzzySearch.
         *
         * @method setMarkersData
         *
         * @param {Array} [Object] Array of markers data to be used by addMarkers method
         */
        setMarkersData: function(points) {
            this._points = points;
            return this;
        },
        /**
         * Adds markers to the layer. Uses options specified by setMarkerOptions and setPopupOptions. Please remember to call addTo(map) after this method
         * if you want to visualize markers on the map.
         *
         * @method addMarkers
         */
        addMarkers: function() {
            var i, marker, markerLayer;
            var options = this.options;
            var points = this._points;
            if (options.noMarkerClustering) {
                this._markersLayer = new L.FeatureGroup();
            } else {
                this._markersLayer = new L.MarkerClusterGroup();
            }

            for (i = 0; i < points.length; ++i) {
                marker = createMarker(points[i], options);
                markerLayer = L.marker(
                        marker.markerPosition, {
                            title: marker.popupHoverContent,
                            popupContent: marker.popupContent,
                            icon: marker.icon
                        }
                    )
                this._markersLayer.addLayer(markerLayer);
                if (markerLayer.options.popupContent instanceof HTMLElement) {
                    markerLayer.bindPopup(markerLayer.options.popupContent.cloneNode(true));
                } else {
                    markerLayer.bindPopup(markerLayer.options.popupContent);
                }
            }
            this.addLayer(this._markersLayer);
            return this;
        },
        /**
         * Returns array of layers which were created by addMarkers method.
         *
         * @method getMarkers
         *
         * @return {Array} Array of marker layers.
         */
        getMarkers: function() {
            if (!this._markersLayer) {
                return [];
            }
            return this._markersLayer.getLayers();
        },

        /**
         * Sets marker options which will be used by addMarkers method
         *
         * @method setMarkerOptions
         *
         * @param {Boolean} options.useBlackIcons If set to true, default icons will be black, else they will be white.
         * @param {Function} options.icon Function which will be called for every point (passed as first argument). Should return an instance of L.Icon or
         * any class which extends L.Icon (e.g. L.SvgIcon)
         * @param {Boolean} options.noMarkerClustering If set to true markers will not be clustered and returned markers layer will be L.FeatureGroup,
         * if set to false markers will be clustered and returned markers layer will be L.markerClusterGroup
         */
        setMarkerOptions: function(opt) {
            L.Util.extend(this.options, opt);
            return this;
        },
        /**
         * @method setPopupOptions
         *
         * @param {Function} options.popupHoverContent Function which will be called for every point (passed as first argument). It allows you to configure the
         * popup's behavior that appears when the cursor moves over the marker. Function must return a string.
         * @param {Function} options.popupContent Function which will be called for every point (passed as first argument). It allows to configure popup content of
         * every marker. This function should return a HTMLElement, String or Leaflet Popup element.
         */
        setPopupOptions: function(opt) {
            L.Util.extend(this.options, opt);
            return this;
        }
    })

    L.TomTomMarkersLayer.addInitHook(function() {
        this._points = [];
        this._markersLayer = undefined;
    });

    /**
     * Instantiates new instance of the TomTomMarkersLayer
     * @method tomTomMarkersLayer
     */
    L.tomTomMarkersLayer = function(options) {
        return new L.TomTomMarkersLayer(options);
    }
    return L.tomTomMarkersLayer;

})
;
